.carousel-control-prev-icon {
  background-color: rgb(255, 153, 0);
  color: white;
}
.carousel-control-next-icon {
  background-color: rgb(255, 153, 0);
  color: white;
}
.text-head {
  color: green;
  text-align: center;
}
.text-container {
  display: flex;
  flex-direction: column;
  padding: 50px 250px;
}
@media (max-width: 380px) {
  .text-head {
    font-size: 26px;
  }
  .text-container {
    font-size: 10px;
    padding: 5px;
  }
}
